<template>
  <div id="formWrapper">
    <form id="frmLogin" ref="frmLogin" @submit.prevent>
      <div class="mb-3">
        <label for="inpEmail" class="form-label">Email</label>
        <input
          type="email"
          class="form-control"
          id="inpEmail"
          ref="inpEmail"
          name="Email"
          v-model="formData.email"
          placeholder="babybo@mail.com"
          aria-describedby="emailHelp"
          required
        />
        <div id="emailHelp" class="form-text">
          本站極易遭駭客攻擊，輸入個資後果自負！
        </div>
      </div>
      <div class="mb-3">
        <label for="inpPassword" class="form-label">密碼</label>
        <input
          type="password"
          class="form-control"
          id="inpPassword"
          ref="inpPassword"
          name="Password"
          v-model="formData.password"
          placeholder="xxxyyy"
          required
        />
      </div>
      <div class="mb-3 form-check">
        <input
          type="checkbox"
          class="form-check-input"
          id="cbRememberMe"
          ref="cbRememberMe"
          name="RememberMe"
          v-model="formData.rememberMe"
        />
        <label class="form-check-label" for="cbRememberMe">勿忘我</label>
      </div>
      <div id="btnWrapper">
        <button
            type="submit"
            class="btn btn-primary"
            id="btnSubmit"
            v-on:click="login()"
          >
            登入
          </button>
        <router-link to="/register" class="btn btn-info">註冊會員</router-link>  
        <button
            class="btn btn-warning"
            id="btnForgetPassword"
            v-on:click="forgetPassword()"
          >
            忘記密碼  
          </button>
      </div>
    </form>

  </div>
</template>

<script>
export default {
  name: "LoginForm",
  data() {
    let model = {
      dataReady: false,
      userInfo:{
        isLogin : false,
        nickName : '',
      },
      formData: {
        email: "",
        password: "",
        rememberMe: false,
      },
    }
    return model;
  },
  async beforeMount(){
      
  },
  methods: {
    async login() {
      /** @type {HTMLFormElement} */
      let frmLogin = this.$refs.frmLogin;
      let isValid = frmLogin.checkValidity();
      if(!isValid){
        return;
      }
      var data = new FormData();
      data.append("Email", this.$data.formData.email);
      data.append("Password", this.$data.formData.password);
      data.append("RememberMe", this.$data.formData.rememberMe);
      var res = await fetch("login.php", {
        method: "POST",
        body: data,
      }).then((response) => response.text());
      if (res == "1|OK") {
        alert("登入成功！伯夷愛貓仙人<3");
        this.$emit('onLogin');
      } else {
        alert("登入失敗！貓仙人森氣氣了=.=");
      }
    },
    
    forgetPassword(){
      this.$router.push('/forgetPassword');
    },
       
  },
};
</script>

<style scoped>
#formWrapper{
    margin: auto;
    margin-top: 50px;
    max-width: 500px;    
}

button{
    margin-right: 5px;
}

#btnWrapper{
    display:flex;
    justify-content: space-evenly;
}
</style>