<template>
    <div>logout</div>
</template>

<script>
export default {
  name: "Logout",  
  async created(){
    var res = await this.logout();
    if(res)
    {
      this.$router.push('/');
    }    
  },

  methods: {
    async logout() {
      let res = await fetch("logout.php", {
        method: "get",
      }).then((response) => response.json());
      if (!res["isLogin"]) {
        this.$emit("userLogin", { isLogin: false, nickname: ''});
      }
      return true;
    },
  },
};
</script>

