<template>
  <div id="headWrapper">
    <h2>會員專區</h2>
  </div>  
  <div v-if="isDataReady">        
    <div id="userInfoWrapper" v-if="userInfo.isLogin">
      <div class="row">
        <div class="col-md-2">
          <label for="userId">Id</label>
        </div>
        <div class="col-md-4">
          <input type="number" v-model="userInfo.id" id="userId" class="form-control" readonly/>
        </div>
        <div class="col-md-2">
          <label for="nickname">䁥稱</label>
        </div>
        <div class="col-md-4">
          <input type="text" v-model="userInfo.nickname" id="nickname" class="form-control"/>
        </div>
      </div>
      <div class="row">
        <div class="col-md-2">
          <label for="email">Email</label>
        </div>
        <div class="col-md-4">
          <input type="email" v-model="userInfo.email" id="email" class="form-control" readonly/>
        </div>
        <div class="col-md-2" id="lblPwd">
          <label for="password">密碼</label>
          <span ref="btnReveal" id="btnReveal" @click="reveal"><i class="fas fa-eye"></i></span>
        </div>
        <div class="col-md-4">
          <input type="password" v-model="userInfo.password" ref="password" id="password" class="form-control"/>
        </div>
      </div>
      <div class="row">
        <div class="col-md-2">
          <label>權限</label>
        </div>
        <div class="col-md-10">
          <input type="checkbox" id="admin" value="1" v-model="userInfo.roles" disabled>
          <label for="admin">管理員</label>
          <input type="checkbox" id="manager" value="2" v-model="userInfo.roles" disabled>
          <label for="manager">經理</label>
          <input type="checkbox" id="user" value="3" v-model="userInfo.roles" disabled>
          <label for="user">會員</label>
        </div>
      </div>
      <div class="row">
        <div class="col-md-4"></div>
        <div class="col-md-4" id="btnWrapper">
          <button class="btn btn-primary" ref="btnEdit" @click="frmSubmit">修改</button>
          <button class="btn btn-secondary" ref="btnCancel">取消</button>
        </div>
        <div class="col-md-4"></div>
      </div>
    </div>
    <div v-else>
      <router-link to="login">訪客請先登入或註冊為本站會員！謝謝！</router-link>
    </div>
  </div>
</template>

<script>

export default {
  name: 'Member',

  data(){
    var data = {
      isDataReady : false,
      userInfo:{
        isLogin : false,
        email : '',
        nickname : '',
        password : '',
        id : '',
        roles : [],
      },
    }
    return data;
  },

  async beforeMount(){
    let token = await this.getMemberInfo();
    if(token.isLogin){
      this.$data.userInfo.isLogin = true;
      this.$data.userInfo.email = token.email;
      this.$data.userInfo.password = token.password;
      this.$data.userInfo.nickname = token.nickname;
      this.$data.userInfo.id = token.id;
      this.$data.userInfo.roles = token.roles;
    }else{
      console.log("logout");
    }
    this.$data.isDataReady = true;
  },

  methods:{

    async getMemberInfo(){
      let token = await fetch('member.php',{
        method: 'get'
      }).then(function(ticket){
        return ticket.json();
      }).catch(function(err){
        console.log(err);
        return {isLogin : false};
      });
      this.$emit('userLogin', {isLogin: token.isLogin, nickname: token.nickname});
      return token;
    },

    reveal(){
      /** @type {HTMLInputElement} */
      let inpPwd =  this.$refs.password;
      if(inpPwd.type == 'password'){
        inpPwd.type = 'text';
      }else{
        inpPwd.type = 'password';
      }
    },

    async frmSubmit(){
      var formData = new FormData();
      formData.append("Password", this.$data.userInfo.password);
      formData.append("Nickname", this.$data.userInfo.nickname);
      let msg = await fetch("updateMember.php",{
        method:'post',
        body: formData,
      }).then(function(res){
        return res.text();
      });
      alert(msg);
      this.$emit("updateMember");
    },

  },
}
</script>

<style scoped>

#headWrapper{
  text-align:center;
}

#userInfoWrapper{
  max-width:750px;
  margin: auto;
  border: 1px solid black;
}

.row{
  margin-left: 0px !important;
  margin-right: 0px !important;
}

#lblPwd{
  display: flex;
  justify-content:space-between;
}

#btnWrapper{
  display:flex;
  justify-content:space-between;
}

</style>
