<template>
    <div class="chat-wrapper">
        <p><span class="nickname">{{nickname}}</span> 說:</p>
        <p class="chat-content" v-html="chatHtml"></p>
        <div class="chat-bottom">
            <div class="created-date">{{createdDate}}</div>
            <a v-if="uid==memberId" @click="btnDelClick($event)" href="#">刪除</a>
        </div>
        <form ref="frm">
            <input ref="chatId" type="hidden" v-model="this.$data.frmData.id" />
        </form>        
        <!-- <h1>{{id}}</h1>
        <h1>{{memberId}}</h1>
        <h1>{{nickname}}</h1>
        <h1>{{chatContent}}</h1>
        <h1>{{uid}}</h1> -->
    </div>    
</template>

<script>
export default {
    props: ['id','uid', 'memberId', 'nickname', 'chatContent', 'createdDate'],
    data(){
        let model = {
            frmData: {
                id : this.id,
            },
            chatHtml : this.$props.chatContent,
        };
        return model;
    },
    methods:{
        async btnDelClick(e){
            e.preventDefault();
            /** @type {HTMLInputElement} */
            let inpChatId = this.$refs.chatId;
            let chatId = inpChatId.value;
            let msg = await fetch(`delChat.php?chatId=${chatId}`,{
                method:'get',
            }).then(function(res){
                return res.json();
            }).catch(function(err){
                console.log(err);
                return {isSuccess:false,errMsg:"刪除失敗！請洽管理員！"};
            });
            alert(msg.errMsg);
            if(msg.isSuccess == true){
                this.$emit("chatDeleted");
            }            
        }
    }
}
</script>

<style scoped>

p{
    margin-bottom:0px;
}

.nickname{
    font-weight:bolder;
}

.chat-content{
    padding:0px 3vw;
    border:black 1px solid;
    margin: 0px 3vw;
}

.created-date{
    text-align: right;
}

.chat-bottom{
    display: flex;
    justify-content: space-between;
    padding: 0px 3vw;
    margin-bottom: 5px;
}

</style>