<template>
  <div id="headWrapper">
    <h2>留言板</h2>
  </div>
  <div id="msgWrapper">
    <div id="borderWrapper">
      <div class="row">
        <div id="btnMinimize">
          <button data-bs-toggle="collapse" data-bs-target="#sendMsgWrapper" aria-expanded="true" aria-controls="sendMsgWrapper">隱藏/展開輸入框</button>
        </div>      
      </div>
      <form ref="frmMsg" @submit.prevent>
        <div id="sendMsgWrapper" class="row collapse show" v-if="userInfo.isLogin">        
            <Editor
              api-key="h2pwmfgv62g9dilzcio6h1bpbpnirj8wcdl81rdyo35inx8n"
              :init="{
                menubar: false,
                plugins: 'autolink wordcount',
                toolbar: 'undo redo',
                default_link_target: '_blank',
                placeholder: '有什麼話想對伯夷說呢？',
              }"
              class="editor"
              v-model="chatContent"
              ref="msgEditor"
            />
            <div style="width:100%;text-align:center;">
              <button type="submit" id="btnSend" @click="sendMsg()">送出</button>
            </div>            
        </div>
        <div id="sendMsgWrapper" class="row collapse show" v-else>
          <textarea class="col-10" placeholder="請先登入或註冊為本站會員" readonly></textarea>
          <button class="col-2" id="btnSend" :disabled="true">發送</button>
        </div>
      </form>
      
    </div>
  </div>
  <div v-if="isDataReady">
    <div class="paging" id="pagingUp"></div>
    <div id="chatArea" ref="chatArea">
      <div v-for="chat in chats" :key="chat.id">
        <Chat @chatDeleted="updateAllChats" :id="chat.id" :uid="this.$data.userInfo.id" :member-id="chat.memberId" :nickname="chat.nickname"
          :chat-content="chat.chatContent" :created-date="chat.createdDate" />
      </div>    
    </div>
    <div class="paging" id="pagingDown"></div>
  </div>
  <div style="text-align:center;" v-else>
    <img class="loading" src="loading.gif"/>
    <p v-if="isFailed">無法取得留言板內容</p>
  </div>
</template>

<script>
import Chat from "../components/Chat.vue";
import Editor from '@tinymce/tinymce-vue'

export default {
  name: 'Discuss',

  data(){
    let model = {
      chatContent:"",
      isDataReady: false,
      isFailed : false,
      userInfo:{
        id : -1,
        nickname:'',
        isLogin: false,
      },
      chats: [],
    };
    return model;
  },

  async beforeMount(){
    let _userInfo = await this.checkLogin();
    if (_userInfo["isLogin"]){
      this.$data.userInfo = _userInfo;
      this.$emit("userLogin", this.$data.userInfo);
    }else{
      
    }
  },

  async mounted(){
    let allChats = await this.getAllChats();
    for(let c of allChats){
      this.$data.chats.push(c);
    }
    if(this.$data.chats.length != 0){
      this.$data.isDataReady = true;
    }else{
      this.$data.isFailed = true;
    }
  },

  methods:{
    async checkLogin(){
      let _userInfo = await fetch("checkLogin.php",{
          method: "GET"
      }).then(function(userdata){
        return userdata.json();
      }).catch(function(err){
        console.log(err);        
        return { nickname:'', isLogin: false};
      });
      return _userInfo;
    },

    sleep(){
      let p = new Promise((resolve, reject) => {
        setTimeout(function(){
          resolve(true);
        }, 500);
      });
      return p;
    },

    async getAllChats(){
      let chats = [];
      let wait = await this.sleep();
      let done = await fetch("chats.php",{
        method:'get',
      }).then(function(jsonStr){
        return jsonStr.json();
      }).then(function(chatsJson){
        chats = chatsJson;
        return true;
      }).catch(function(err){
        console.log(err);
        return true;
      });
      
      if(wait && done){
        return chats;        
      }
      
    },

    async updateAllChats(){
      this.$data.chats.splice(0);
      this.$data.isDataReady = false;
      this.$data.isFailed = false;
      let allChats = await this.getAllChats();
      for(let c of allChats){
        this.$data.chats.push(c);
      }
      if(this.$data.chats.length != 0){
        this.$data.isDataReady = true;
        this.$data.isFailed = false;
      }else{
        this.$data.isDataReady = false;
        this.$data.isFailed = true;
      }
    },

    async sendMsg(){
      let tinymce = window.tinymce;
      let wordcount = parseInt(tinymce.activeEditor.plugins.wordcount.body.getWordCount());
      if(wordcount <= 0){
        alert("請輸入您想對伯夷說的話！");
        return;
      }else{
        let formData = new FormData();
        formData.append("ChatContent", this.$data.chatContent);
        let msg = await fetch("sendChat.php", {
          method: "POST",
          body: formData,
        }).then(function(res){
          return res.json(); 
        }).catch(function(err){
          console.log(err);
          return {isSuccess: false, errMsg: "張貼失敗！請重新登入會員或洽詢管理員"};
        });
        if(msg.isSuccess){
          tinymce.activeEditor.setContent("");
          await this.updateAllChats();
        }
        alert(msg.errMsg);
      }
      
    },
  },

  components:{Chat, Editor},
}
</script>

<style scoped>

#headWrapper{
  text-align:center;
}

#msgWrapper{
  position: relative;
}

#borderWrapper{
  border : 1px solid black;
}

#sendMsgWrapper{
  padding: 5px;
}

.editor{
  height: 15vh;
}

#btnSend{
  position: relative;
}

#btnMinimize{
  padding:0px;
  text-align: center;
}

.loading{
  margin-top: 20vh;
  max-width: 10vw;
}

.paging{
  text-align: center;
}

#chatArea{
   
  
}
</style>
