<template>
  <div id="headWrapper">
    <h2>註冊會員</h2>
  </div>
  <form id="frmRegister" ref="frmRegister" method="post" @submit.prevent>
    <div class="row">
      <div class="col-md-2">
        <label>*Email</label>
      </div>
      <div class="col-md-4">
        <input type="email" id="email" name="email" class="form-control" required/>
      </div>
      <div class="col-md-2">
        <label>暱稱</label>
      </div>
      <div class="col-md-4">
        <input type="text" id="nickname" name="nickname" class="form-control" />
      </div>
    </div>
    <div class="row">
      <div class="col-md-2">
        <label>*密碼</label>
      </div>
      <div class="col-md-4">
        <input type="password" id="password" name="password" class="form-control" required/>
      </div>
      <div class="col-md-2">
        <label>*再次輸入密碼</label>
      </div>
      <div class="col-md-4">
        <input type="password" id="password2" name="password2" class="form-control" required/>
      </div>
    </div>
    <div class="row">
        <p id="warning">注意！本站會員密碼寫入資料庫前不會先hash過（明碼），所以請勿使用自己常用的密碼來註冊！</p>
    </div>
    <div class="row">
        <div class="col-md-4"></div>
        <div class="col-md-4" id="btnWrapper">
          <button class="btn btn-primary" ref="btnSend" type="submit" :disabled="btnDisabled" @click="register()">註冊</button>
          <button class="btn btn-secondary" ref="btnCancel" type="reset">清除</button>
        </div>
        <div class="col-md-4"></div>
    </div>
    <div id="errMsg" ref="errMsg"></div>
  </form>
</template>

<script>

export default {
  name: 'Register',

  data(){
    let model ={

      btnDisabled : false,

      responseMsg:{
        isSuccess : false,
        errMsg : '',
      },

    };
    return model;
  },

  methods:{
    async register(){
      this.$data.btnDisabled = true;

      this.$refs.errMsg.innerHTML = this.$data.responseMsg.errMsg;
      /** @type {HTMLFormElement} */
      let frmRegister = this.$refs.frmRegister;
      let formData = new FormData(this.$refs.frmRegister);
      let isValid = frmRegister.checkValidity();
      if(!isValid){
        this.$data.responseMsg.isSuccess = false;
        this.$data.responseMsg.errMsg = "請輸入必填資料！";
        this.$refs.errMsg.innerHTML = this.$data.responseMsg.errMsg;
        this.$data.btnDisabled = false;
        return;
      }else if(formData.get("password")!=formData.get("password2")){
        this.$data.responseMsg.isSuccess = false;
        this.$data.responseMsg.errMsg = "密碼不一致！";
        this.$refs.errMsg.innerHTML = this.$data.responseMsg.errMsg;
        this.$data.btnDisabled = false;
        return;
      }
      let res = await fetch("register.php", {
        method: "POST",
        body: formData,
      }).then((response) => response.json());
      this.$data.responseMsg = res;
      
      if(res["isSuccess"]){
        this.$refs.errMsg.innerHTML = this.$data.responseMsg.errMsg;
      }else{
        this.$refs.errMsg.innerHTML = this.$data.responseMsg.errMsg;
      }
    },
  },

}
</script>

<style scoped>

.row{
  margin-top: 10px;
  margin-left: 0px !important;
  margin-right: 0px !important;
}

#headWrapper{
  text-align:center;
}

#frmRegister{
  border: 1px solid black;  
  margin: auto;
  max-width: 750px;
}

#btnWrapper{
  display:flex;
  justify-content:space-between;
}

#warning{
  color:red;
  font-style: italic;
  font-weight: bolder;
}

#errMsg{
  color:red;
  font-style: italic;
  font-weight: bolder;
  text-align: center;
}

</style>
