<template>
  <div id="headWrapper">
    <h2>註冊會員</h2>
  </div>
  <div id="formWrapper">
    <form id="frmForgetPassword" ref="frmForgetPassword" @submit.prevent>
      <div class="row">
        <div class="col">
          <label for="email">請輸入您註冊時所使用的箱信：</label>
        </div>
        <div class="col">
          <input class="form-control" type="email" name="Email" id="email"
          placeholder="email" 
          v-model="this.$data.formData.email" required />
        </div>
        <div class="col">
          <button class="btn btn-outline-warning" type="button" ref="btnSubmit" :disabled="btnDisabled" @click="resetPassword($event)">重設密碼</button>
        </div>
      </div>
      <div class="row" id="warning" ref="warning">
      </div>
    </form>
    
  </div>
</template>

<script>
// @ is an alias to /src

export default {
  name: "ForgetPassword",
  data() {
    let model = {

      btnDisabled : false,
      
      formData:{
        email:'',
        isValid : '',
      }      
    };
    return model;
  },

  async beforeMount() {},

  methods: {
    async checkLogin() {
      let _userInfo = await fetch("checkLogin.php", {
        method: "GET",
      }).then((res) => res.json());
      return _userInfo;
    },

    /** 
     * @param e {MouseEvent}
     */
    async resetPassword(e){
      this.$data.btnDisabled = true;      
      e.preventDefault();

      /** @type {HTMLDivElement} */
      let warningDiv = this.$refs.warning;
      warningDiv.innerHTML = '';
      
      let frmForgetPassword = this.$refs.frmForgetPassword;      
      let isValid = frmForgetPassword.checkValidity();
      if(!isValid){
        warningDiv.innerHTML = "請輸入您註冊時所使用的信箱！";
        this.$data.btnDisabled = false;
        return;
      }else{
        this.$data.formData.isValid = true;
        let data = new FormData();
        data.append("Email",this.$data.formData.email);
        let msg = await fetch("forgetPassword.php", {
          method: "POST",
          body: data,
        }).then(function(res){
          return res.json(); 
        });
        warningDiv.innerHTML = msg.errMsg;
        return;
      }      
    },
  },
};
</script>

<style scoped>
#headWrapper{
  text-align:center;
}

#frmForgetPassword{
  border: 1px solid black;  
  margin: auto;
  max-width: 750px;
}

.row > .col{
  display: flex;
  align-items: center;
}

#warning{
  color:red;
  font-style: italic;
  font-weight: bolder;
}
</style>