<template>
  <div id="flexWrapper">
    <div id="topBarWrapper">
      <button class="btn btn-success" id="btnToggleSideBar">
        <i class="fas fa-bars"></i>
      </button>
      <div id="loginWrapper">
        <div v-if="userInfo.isLogin">
          哈囉~~ {{ this.$data.userInfo.nickname }}！
        </div>
        <i class="fas fa-user-alt"></i>
        <router-link to="/logout" v-if="userInfo.isLogin">登出</router-link>
        <router-link to="/login" v-else>登入</router-link>
        <i class="fas fa-chalkboard-teacher"></i>
        <router-link to="/register">註冊</router-link>
      </div>
    </div>
    <div id="contentWrapper">
      <div id="sideBar" class="side-bar" style="display:none;">
        <div class="nav-link">
          <router-link class="my-link" to="/">首頁</router-link>
        </div>
        <div class="nav-link">
          <router-link class="my-link" to="/discuss">留言板</router-link>
        </div>
        <div class="nav-link">
          <a href="https://bbljchatroom.azurewebsites.net/" target="_blank">即時聊天室</a>
        </div>
        <div class="nav-link">
          <router-link class="my-link" to="/member">會員專區</router-link>
        </div>
      </div>
      <div id="main">
        <!-- @RenderBody() -->
        <router-view @userLogin="onUserLogin" @updateMember="onUpdateMember"/>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",

  data() {
    let model = {
      userInfo: {
        isLogin: false,
        nickname: "",
      },
      
      isDataReady: false,
    };
    return model;
  },

  async beforeMount() {
    
  },

  methods: {
    onUserLogin(_userInfo) {
      if (_userInfo.isLogin) {
        this.$data.userInfo = _userInfo;
      } else {
        this.$data.userInfo.isLogin = false;
        this.$data.userInfo.nickname = "";
      }
    },
    
    async onUpdateMember(){
      let _userInfo = await fetch("checkLogin.php",{
          method: "GET"
      }).then(function(userdata){
        return userdata.json();
      }).catch(function(err){
        console.log(err);        
        return { nickname:'', isLogin: false};
      });
      this.$data.userInfo = _userInfo;
    }
  },
};
</script>
